}



html, body
{
    height: 100%;
}
.wrapper {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: 100%;
    height: 500vh;

    -webkit-align-items: stretch;

            align-items: stretch;
}

#sidebar {
    min-width: 20vw;
    max-width: 20vw;
    min-height: 100vh;
}

body {
    font-family: 'Poppins', sans-serif;
    background: #fafafa;
}

.components{
    font-weight: 900;

}

p {
    font-family: 'Poppins', sans-serif;
    font-size: 1.1em;
    line-height: 1.7em;
    color: #999;
}



a, a:hover, a:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.3s;
    font-weight: 300;
}

#sidebar {
    /* don't forget to add all the previously mentioned styles here too */
    background: #7386D5;
    color: #fff;
    transition: all 0.3s;
}

#sidebar .sidebar-header {
    padding: 22px;
    background: #6d7fcc;
}

#sidebar ul.components {
    border-bottom: 1px solid #47748b;
}

#sidebar ul p {
    color: #fff;
    padding: 10px;
}

#sidebar ul li a {
    padding: 10px;
    font-size: 1.1em;
    display: block;
}
#sidebar ul li a:hover {
    color: #7386D5;
    background: #fff;
}

#sidebar ul li.active > a, a[aria-expanded="true"] {
    color: #fff;
    background: #6d7fcc;
}


ul ul a {
    font-size: 0.9em !important;
    padding-left: 30px !important;
    background: #6d7fcc;
}



